@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
*,*:before,*:after{box-sizing:border-box}html,body,div,span,object,iframe,figure,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,code,em,img,small,strike,strong,sub,sup,tt,b,u,i,ol,ul,li,fieldset,form,label,table,caption,tbody,tfoot,thead,tr,th,td,main,canvas,embed,footer,header,nav,section,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:none;-moz-text-size-adjust:none;-ms-text-size-adjust:none;text-size-adjust:none}footer,header,nav,section,main{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}input{-webkit-appearance:none;border-radius:0}html{scroll-behavior:smooth}body{background:#ededed;font-family:'Roboto', sans-serif}.root{display:flex;flex-direction:column;height:100vh}

.header{display:flex;justify-content:space-between;align-items:center;padding:14px 40px;background:#f58c23}.header__title{font-size:24px;font-weight:bold}.header__btn{cursor:pointer;background:none;border:none;height:30px;padding:0}.header__btn__icon{height:100%;width:auto}

.fake-content{display:flex;margin:0 11px;padding:24px 10px 15px;border-bottom:1px solid #ededed}.fake-content__number{margin-right:8px;background:#9b9b9b;height:20px;width:20px;border-radius:50%}.fake-content__data{flex:1 1}.fake-content__title{height:20px;width:70%;background:#b0b0b0;margin-bottom:13px}.fake-content__info{height:16px;width:40%;background:#b0b0b0}

.news-item{display:flex;margin:0 11px;padding:24px 10px 15px;border-bottom:1px solid #ededed}.news-item__number{font-size:20px;color:#9b9b9b;margin-right:8px}.news-item__data{flex:1 1}.news-item__title{font-size:16px;margin-bottom:13px}@media (min-width: 576px){.news-item__title{font-size:20px}}.news-item__title__link{color:inherit;text-decoration:none}.news-item__title__link:visited{color:#828282}.news-item__title__source{color:#a0a0a0;display:inline-block;text-decoration:none}.news-item__info{font-size:11px}@media (min-width: 576px){.news-item__info{font-size:16px}}.news-item__info__text--gray{color:#b0b0b0}.news-item__info__link{color:inherit;text-decoration:none}.news-item__info__link:hover{text-decoration:underline}.news-item__comments{color:inherit;text-decoration:none}.news-item__comments:hover{text-decoration:underline}

.paging{color:#c1c1c1}.paging__btn{font-size:18px;cursor:pointer;border:none;background:none}

.icon{display:flex;align-items:center;justify-content:center;flex-basis:calc(1em + 4px)}@supports (display: grid){.icon{flex-basis:auto}}.icon svg{height:1em}.icon .st0{fill:currentColor}

.news-list{background:#fff;margin:0;flex:1 1;padding-bottom:40px}@media (min-width: 576px){.news-list{margin:0 20px;font-size:20px}}.news-list__spinner{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.news-list__spinner svg{width:100px;height:auto}.news-list .paging{margin-top:40px;margin-left:56px}

